html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slider {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  height: 100vh;
}

.slider__wrap {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  padding: 0 50px;
}

.slider__main {
  padding-bottom: 10px;
  position: relative;
  width: 100%;
  margin: 0 auto;
}

.slider__item {
  display: inline-flex !important;
  justify-content: center;
  align-items: flex-end;
  width: 150px !important;
  height: 150px;
  margin: 0 25px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  border: 3px solid rgba(255, 255, 255, 0.4);
  cursor: pointer;
  line-height: 0;
  transition: .3s ease;
}

.slider__item:hover {
  border: 5px solid white;
  box-shadow: 0 7px 15px 0 rgba(32, 128, 122, 0.75);
}

.slider__item:hover > .slider__image {
  animation: jumpUp .5s linear;
  transform: translate3d(0, -12px, 0) scale3d(1, 1.15, 1);
}

.slider__image {
  transition: .3s ease;
}

.slider__controls {
  display: flex;
  justify-content: space-between;
}

.slider__controls__prev {
  display: inline-block;
  width: 160px;
  cursor: pointer;
  padding: 20px 35px 20px 40px;
  position: absolute;
  z-index: 1;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  perspective: 300px;
  color: #F5F8F7;
  transition: .2s ease;
  text-align: right;
}

.slider__controls__prev::before {
  content: '';
  position: absolute;
  z-index: -1;
  top: 3px;
  left: 0;
  width: 100%;
  height: 90%;
  border: 2px solid #F5F8F7;
  background: none;
  box-sizing: border-box;
  border-radius: 5px;
  transform: translateZ(-40px) rotateY(20deg) rotateX(0deg) rotateZ(-1deg) translateX(10px);
  transition: 0.4s cubic-bezier(0.1, 0.5, 0.1, 1);
}

.slider__controls__prev:hover {
  color: #22b8b3;
}

.slider__controls__prev:hover::before {
  background: #F5F8F7;
  transform: rotateY(-20deg);
}

.slider__controls__prev:hover .slider__controls__btn--prev::after {
  border-color: #22b8b3;
  left: -20px;
}

.slider__controls__next {
  display: inline-block;
  width: 160px;
  cursor: pointer;
  padding: 20px 50px 20px 40px;
  position: absolute;
  z-index: 1;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  perspective: 300px;
  color: #F5F8F7;
  transition: .2s ease;
}

.slider__controls__next::before {
  content: '';
  position: absolute;
  z-index: -1;
  top: 3px;
  left: 0;
  width: 100%;
  height: 90%;
  border: 2px solid #F5F8F7;
  background: none;
  box-sizing: border-box;
  border-radius: 5px;
  transform: translateZ(-40px) rotateY(-20deg) rotateX(0deg) rotateZ(-1deg) translateX(-10px);
  transition: 0.4s cubic-bezier(0.1, 0.5, 0.1, 1);
}

.slider__controls__next:hover {
  color: #22b8b3;
}

.slider__controls__next:hover::before {
  background: #F5F8F7;
  transform: rotateY(20deg);
}

.slider__controls__next:hover .slider__controls__btn--next::after {
  border-color: #22b8b3;
  right: -20px;
}

.slider__controls__btn {
  font-size: 18px;
  font-family: "Myriad Pro", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
}

.slider__controls__btn--next::after {
  content: '';
  position: absolute;
  z-index: 2;
  border-right: 3px solid #fff;
  border-bottom: 3px solid #fff;
  width: 9px;
  height: 9px;
  transform: translateY(-50%) rotate(-45deg);
  top: 43%;
  right: -13px;
  transition: .3s ease;
}

.slider__controls__btn--prev::after {
  content: '';
  position: absolute;
  z-index: 2;
  border-right: 3px solid #fff;
  border-bottom: 3px solid #fff;
  width: 9px;
  height: 9px;
  transform: translateY(-50%) rotate(135deg);
  top: 43%;
  left: -13px;
  transition: .3s ease;
}

.slider .slick-list {
  display: flex;
  align-items: center;
  height: 270px;
}

.slider .slick-prev,
.slider .slick-next {
  display: none !important;
}

.slider .slick-slide:not(.slick-active) {
  display: none;
  transition: .5s linear;
}

.slider .slick-slide.fadeInLeft {
  -webkit-animation: scaleOut .8s linear;
  -o-animation: scaleOut .8s linear;
  animation: scaleOut .8s linear;
  transform-origin: center;
}

.slider .slick-slide.fadeOutRight {
  -webkit-animation: scaleIn .6s linear;
  -o-animation: scaleIn .6s linear;
  animation: scaleIn .6s linear;
  transform-origin: center;
}

@keyframes jumpUp {
  0% {
    transform: translate3d(0, -3px, 0) scale3d(1, 1.05, 1);
  }
  50% {
    transform: translate3d(0, -7px, 0) scale3d(1, 1.1, 1);
  }
  75% {
    transform: translate3d(0, -16px, 0) scale3d(1, 1.2, 1);
  }
  100% {
    transform: translate3d(0, -12px, 0) scale3d(1, 1.15, 1);
  }
}

@keyframes scaleOut {
  0% {
    transform: translateX(-400px) scale(1.4);
  }
  50% {
    transform: translateX(-150px) scale(1.3);
  }
  75% {
    transform: translateX(-50px) scale(1.2);
  }
  100% {
    transform: translateX(0px) scale(1);
  }
}

@keyframes scaleIn {
  0% {
    transform: translateX(0px) scale(1);
  }
  50% {
    transform: translateX(50px) scale(1.2);
  }
  75% {
    transform: translateX(150px) scale(1.3);
  }
  100% {
    transform: translateX(400px) scale(1.4);
  }
}

@font-face {
  font-family: 'Myriad Pro';
  font-weight: normal;
  src: url("../fonts/MyriadPro.eot");
  src: url("../fonts/MyriadPro.eot?#iefix") format("embedded-opentype"), url("../fonts/MyriadPro.woff") format("woff"), url("../fonts/MyriadPro.ttf") format("truetype");
}
